import axios from 'axios';

const API_BASE_URL = 'http://localhost:8000';

// 文献API服务
export class LiteratureAPI {
  // 获取认证头
  private static getAuthHeaders() {
    const token = localStorage.getItem('token');
    return {
      'Authorization': `Bearer ${token}`,
    };
  }

  // 上传私人文献
  static async uploadPrivateLiterature(file: File, title?: string) {
    const formData = new FormData();
    formData.append('file', file);
    if (title) {
      formData.append('title', title);
    }

    const response = await axios.post(
      `${API_BASE_URL}/literature/upload/private`,
      formData,
      {
        headers: {
          ...this.getAuthHeaders(),
          'Content-Type': 'multipart/form-data',
        },
      }
    );
    return response.data;
  }

  // 上传课题组文献
  static async uploadGroupLiterature(file: File, groupId: string, title?: string) {
    const formData = new FormData();
    formData.append('file', file);
    formData.append('group_id', groupId);
    if (title) {
      formData.append('title', title);
    }

    const response = await axios.post(
      `${API_BASE_URL}/literature/upload`,
      formData,
      {
        headers: {
          ...this.getAuthHeaders(),
          'Content-Type': 'multipart/form-data',
        },
      }
    );
    return response.data;
  }

  // 获取私人文献列表
  static async getPrivateLiterature() {
    const response = await axios.get(
      `${API_BASE_URL}/literature/private`,
      {
        headers: this.getAuthHeaders(),
      }
    );
    return response.data;
  }

  // 获取课题组文献列表
  static async getGroupLiterature(groupId: string) {
    const response = await axios.get(
      `${API_BASE_URL}/literature/public/${groupId}`,
      {
        headers: this.getAuthHeaders(),
      }
    );
    return response.data;
  }

  // 获取用户课题组列表
  static async getUserGroups() {
    const response = await axios.get(
      `${API_BASE_URL}/user/groups`,
      {
        headers: this.getAuthHeaders(),
      }
    );
    return response.data;
  }
}

// 获取文献详情
export const getLiteratureDetail = async (literatureId: string) => {
  return axios.get(`/literature/detail/${literatureId}`);
};

// 预览文献
export const previewLiterature = async (literatureId: string) => {
  return axios.get(`/literature/view/file/${literatureId}`, {
    responseType: 'blob' // 处理二进制响应
  });
};

// 下载文献
export const downloadLiterature = async (literatureId: string) => {
  return axios.get(`/literature/download/${literatureId}`, {
    responseType: 'blob'
  });
};